body {
  font-family: 'Roboto', sans-serif;
  background-color: #f3f4f6;
  color: #1f2937;
  margin: 0;
  padding: 0;
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}
nav {
  background-color: #1e40af;
  color: white;
  padding: 1rem;
}
nav a {
  transition: color 0.3s ease;
}
nav a:hover {
  color: #d1d5db;
}
button {
  transition: background-color 0.3s ease, opacity 0.3s ease;
}
button:hover {
  opacity: 0.9;
}
.search-bar input {
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  padding: 0.5rem 1rem;
  margin-right: 0.5rem;
}
.search-bar input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}
.flight-card {
  background-color: white;
  border-radius: 0.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}
.flight-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}
.progress-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 2rem 0;
}
.progress-step {
  flex: 1;
  text-align: center;
  padding: 0.5rem;
  border-bottom: 2px solid #d1d5db;
  color: #9ca3af;
}
.progress-step.active {
  border-bottom-color: #3b82f6;
  color: #3b82f6;
}
form input {
  width: 100%;
  padding: 0.5rem;
  margin-bottom: 1rem;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
}
form input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}
form button {
  width: 100%;
  padding: 0.75rem;
  background-color: #3b82f6;
  color: white;
  border: none;
  border-radius: 0.375rem;
  cursor: pointer;
}
form button:hover {
  background-color: #2563eb;
}
.text-blue-600 {
  color: #2563eb;
}
.bg-blue-600 {
  background-color: #2563eb;
}
.bg-gray-100 {
  background-color: #f3f4f6;
}
.text-gray-500 {
  color: #6b7280;
}
.rounded-lg {
  border-radius: 0.5rem;
}
.shadow-lg {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}
.modal {
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  width: 100%;
  padding: 20px;
  animation: slide-up 0.3s ease-out;
}
@keyframes slide-up {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.modal button {
  margin-top: 10px;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.modal button.bg-green {
  background-color: #38a169;
  color: white;
}
.modal button.bg-red {
  background-color: #e53e3e;
  color: white;
}
